import { GET_RECETAS } from "../Actions/actions";



const initialState={
    recipes : [],
    typediet :[],

}

export default function rootReducer (state = initialState,actions){
    switch (actions.type) {
        case GET_RECETAS:
            return({
                ...state,
                recipes : actions.payload
            })
            
            
    
        default:
            return state
            
    }
}